import React from 'react' 
import './PortfolioItem.css'

export const PortfolioItem = item => {
    return (
        <div className="portfolio__item">
            <h2 className="portfolio__title">{ item.title }</h2>
            <p className="portfolio__subtitle">{ item.subtitle }</p>
            <p className="portfolio__description">{ item.description }</p>
            <div className="portfolio__images">
                { item.images.map(link => <img className="portfolio__image" key={ link } src={ link } alt={ link } /> ) }
            </div>
        </div>
    )
}