import React from 'react'
import { Helmet } from 'react-helmet'
import './AboutMePage.css'

export const AboutMePage = () => {
    return (
        <div className="aboutme">
            <Helmet title="Карамова Лилия | Обо Мне" />

            <h1 className="aboutme__title">Обо Мне</h1>
            <p className="aboutme__text">
                <span className="aboutme__text-paragraph">Карамова Лилия Рафиковна. 47 лет.</span> 
                <span className="aboutme__text-paragraph">Образование высшее(БирГПИ, факультет дошкольной педагогии и психологии) и Профессиональная переподготовка по Специальной педагогике и психологии(БИРО г. Уфа). Работаю по специальности Воспитатель.</span> 
                <span className="aboutme__text-paragraph">Стаж работы 25 лет, из них 2 года работа в коррекционной группе детского сада.</span>
            </p>
        </div>
    )
}