import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import './Burger.css'

export const Burger = () => {
    const [open, setOpen] = useState(false)
    const burgerFieldClass = ['burger-field']

    const changeOpenHandler = () => {
        setOpen(prev => !prev)
    }

    if(open) {
        burgerFieldClass.push('burger-field--active')
    }

    return (
        <div className={ burgerFieldClass.join(' ') }>
            <nav className="burger">
                <div 
                    className="burger-btn"
                    onClick={ changeOpenHandler }
                >
                    <span className="burger-line1"></span>
                    <span className="burger-line2"></span>
                    <span className="burger-line3"></span>
                </div>

                <ul className="burger__list">
                    <li 
                        className="burger__link"
                        onClick={ changeOpenHandler }
                    >
                        <NavLink to="/home">Главная</NavLink>
                    </li>

                    <li 
                        className="burger__link"
                        onClick={ changeOpenHandler }
                    >
                        <NavLink to="/aboutme">Обо Мне</NavLink>
                    </li>

                    <li 
                        className="burger__link"
                        onClick={ changeOpenHandler }
                    >
                        <NavLink to="/portfolio">Портфолио</NavLink>
                    </li>
                </ul>
            </nav>
        </div>
    )
}