import React from 'react'
import { Helmet } from 'react-helmet'
import { PortfolioItem } from '../../components/Portfolio/PortfolioItem'
import './PortfolioPage.css'

export const PortfolioPage = () => {
    const portfolios = [
        {
            id: Math.random() * 5 + Date.now(),
            title: 'Выставка работ на тему "Дары Осени"',
            subtitle: 'Творческая работа учащихся по конструированию, ручному труду',
            description: 'Представлены работы воспитанников подготовительной группы и их родителей',
            images: [
                'https://nsportal.ru/sites/default/files/2020/10/17/img_20200923_170756.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/img_20200921_081317.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181002_134007.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181002_134620.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181001_102048.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181001_102024.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181001_102010.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20181001_102031.jpg'
            ]
        },
        {
            id: Math.random() * 5 + Date.now(),
            title: 'Оформление к конкурсу "Новогодний серпантин"',
            subtitle: 'Материал по конструированию, ручному труду (старшая группа)',
            description: 'В преддверии Новогодних праздников вместе с родителями оформили, украсили групповую комнату и раздевалку нашей группы. Получилось здорово! Заняли 1-ое место!',
            images: [
                'https://nsportal.ru/sites/default/files/2020/10/17/20191220_073708.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20191220_072840.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20191220_073057.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20191224_152424.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20191224_152230.jpg',
                'https://nsportal.ru/sites/default/files/2020/10/17/20191220_072926.jpg',
            ]
        }
    ]

    return (
        <div className="portfolioPage">
            <Helmet title="Карамова Лилия | Портфолио" />
            <h1 className="portfolioPage__title">Портфолио</h1>

            <div className="portfolio">
                { portfolios.map(el => <PortfolioItem key={ el.id } {...el} /> ) }
            </div>
        </div>
    )
}